$width: min(10vw, 50px);
$gap: min(2vw, 10px);
$width-half: min(5vw, 25px);
@debug $width;

body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.screen {
  width: 100vw;
  height: 100vh;
  background-color: #274a60;
  //background-image: linear-gradient(180deg, #8BC6EC 0%, #9599E2 50%);
  overflow: hidden;
  display: flex;
  align-items: center;
  flex-direction: column;
}

.title-container {
  padding: 10vh;
  width: 100%;
  box-sizing: border-box;
}

.title {
  font-size: min(5vw, 30px);
  font-weight: 100;
  text-transform: uppercase;
  color: white;
}

.subtitle {
  font-size: min(4vw, 25px);
  font-weight: 200;
  color: white;
}

.chart-container {
  height: 100%;
  margin-bottom: 10vh;
  display: flex;
  align-items: flex-end;
  position: relative;
}

.chart {
  display: flex;
  position: absolute;
  display: flex;
  justify-content: center;
  bottom: $width-half;
}

.columns {
  display: flex;
  align-items: flex-end;
  gap: $gap;
}

.column-title {
  position: absolute;
  transform: rotateZ(270deg);
  white-space: nowrap;
  transform-origin: 0% 50%;
  bottom: -5px;
  left: 50%;
  color: white;
  text-transform: uppercase;
  font-size: min(5vw, 30px);
}

.column {
  height: 100px;
  width: $width;
  background: red;
  position: relative;
  border-top-left-radius: $width-half;
  border-top-right-radius: $width-half;
  overflow: hidden;
}

.value-container {
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  width: $width;
  height: $width;
  position: relative;
  border-radius: 50%;
}

.value {
  white-space: nowrap;
  color: white;
  text-transform: uppercase;
  font-size: min(5vw, 30px);
}

.circle-container {
  display: flex;
  gap: $gap;
}

.circle {
  width: $width;
  height: $width;
  border-radius: 50%;
}

